#_::before{
  content: "< ";
}
#f{
  margin-left: -6px;
}
#c{
  color:rgba(161, 148, 148, 0.759);
}
#s{
  color:rgba(172, 39, 39, 0.864);
}

body {
  background: #282c34; /* Fallback */

  padding: 2em;
}
h1{
  font-family: 'Kavoon', sans-serif;
  font-size: 70px;
  margin-top: 5%;
  margin-left: 6%;
}
h3{
  font-family: 'Kavoon', sans-serif;
  margin-top: 10%;
  margin-left: 6%;
  font-size: 20px;
  color:rgb(244, 242, 242);
}


.context {
  width: 100%;
  position: absolute;
  top:50vh;
  
}

.context h1{
  text-align: center;
  color: #fff;
  font-size: 50px;
}


.area{
  background: #282c34;;   
}

.circles{
  position: absolute;
  top: 0;
  left: 0;
  width: 95%;
  height: 95%;
  overflow: hidden;
}

.circles li{
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
}
.circles li:nth-child(1):hover{
background-color: #282c34;
}
.circles li:nth-child(2):hover{
  background-color: #282c34;
  }
  .circles li:nth-child(3):hover{
    background-color: #282c34;
    }
    .circles li:nth-child(4):hover{
      background-color: #282c34;
      }
      .circles li:nth-child(5):hover{
        background-color: #282c34;
        }
        .circles li:nth-child(6):hover{
          background-color: #282c34;
          }
          .circles li:nth-child(7):hover{
            background-color: #282c34;
            }
            .circles li:nth-child(8):hover{
              background-color: #282c34;
              }
              .circles li:nth-child(9):hover{
                background-color: #282c34;
                }
                .circles li:nth-child(10):hover{
                  background-color: #282c34;
                  }
.circles li:nth-child(1){
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}


.circles li:nth-child(2){
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3){
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4){
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5){
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6){
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7){
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8){
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9){
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10){
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}



@keyframes animate {

  0%{
      transform: translateY(0) rotate(0deg);
      opacity: 1;
      border-radius: 0;
  }

  100%{
      transform: translateY(-1000px) rotate(720deg);
      opacity: 0;
      border-radius: 50%;
  }

}